<template>
  <div id="EFiles">
    <h2>Eficácia</h2>

    <div class="box_files">
      <!-- <carousel :items="mobile ? 1 : 4" :nav="false">
            <div v-for="(d,index) in files" :key="index" class="file">
                <a href="#" @click.stop="open(d)">
                    <img  :src ="d.thumb" />
                    <legend>{{d.name}}</legend>
                </a>                
            </div>
      </carousel>-->

      <div v-for="(d,index) in files" :key="index" class="file">
        <span class="link" @click.stop="open(d)">
          <img :src="d.thumb">
          <legend v-html="d.name"></legend>
        </span>
      </div>
    </div>
    <b-modal id="pdf-object" :title="title" size="xl" centered v-model="pdfObject">
      <object
        type="application/pdf"
        :data="pdf"
        width="100%"
        height="500"
        style="height: 75vh;"
        v-if="!mobile"
      >Navegador não tem suporte a PDF</object>
      <iframe
        :src="'https://docs.google.com/viewer?url=http://18.229.92.132/pwa_idal/' + pdf + '&embedded=true'"
        v-if="mobile"
        width="100%"
        height="500"
        frameborder="0"
      ></iframe>
      <div slot="modal-footer" class="w-100">
        <b-button
          variant="dark"
          size="sm"
          class="float-right"
          @click="$bvModal.hide('pdf-object')"
        >Fechar</b-button>
        <a :href="pdf" :download="title" class="btn float-right btn-dark btn-sm download">Download</a>
      </div>
    </b-modal>
  </div>
</template>
<script>
import isMobile from "mobile-device-detect";
import carousel from "vue-owl-carousel";

export default {
  components: {
    carousel
  },
  data: () => ({
    files: [
      {
        name:
