        uri: "dist/files/Eficacia da vacina-4.pdf",
        thumb: require("@/assets/thumb/Eficacia-da-vacina-4.png")
      },
      {
        name:
          "A eficácia da vacinação intradérmica é superior ou igual as outras estratégias convencionais de controle do <i>Mycoplasma hyopenumonaie</i>",
        uri: "dist/files/Eficacia da vacina-5.pdf",
        thumb: require("@/assets/thumb/Eficacia-da-vacina-5.png")
      },
      {
        name:
          "Melhor desempenho após o uso simultâneo das vacinas Porcilis® M1 ID e Porcilis® PCV ID",
        uri: "dist/files/Eficacia da vacina-6.pdf",
        thumb: require("@/assets/thumb/Eficacia-da-vacina-6.png")
      },
      {
        name:
