  overflow: auto;
  margin: 110px auto;
  padding: 0 10%;

  .link {
    cursor: pointer;
    color: #028398;
    display: block;
    overflow: auto;

    &:hover {
      color: #074852;
    }
  }

  .file {
    // margin: 20px;
    float: left;
    width: 25%;
    padding: 5px 30px;
  }

