<template>
  <b-container>
    <b-breadcrumb :items="items"></b-breadcrumb>
    <b-row style="margin-top:75px;">
      <b-col>
        <h1>Porcilis® M1 ID</h1>
        <div>Vacina inativada contra Pneumonia Enzoótica dos Suínos. Via de aplicação intradérmica.</div>
        <img src="@/assets/vacinas/porcilis-m1-id-10ml.png">
      </b-col>
    </b-row>
    <b-row>
      <div class="all-product-rows">
        <div class="product-top-section">
          <div class="product-top-label-description">
            <div class="product-top-description">
            </div>
          </div>
        </div>
        <div>
          <div class="paragraph-header">
            <span>Características:</span>
          </div>
          <div class="paragraph-body">
            <span>Vacina inativada contra Pneumonia Enzoótica dos Suínos.</span>
            <br>
          </div>
        </div>
        <div>
          <div class="paragraph-header">
            <span>Indicação:</span>
          </div>
          <div class="paragraph-body">
            <span>
              Para a imunização ativa de suínos saudáveis com duas semanas de idade ou mais, para redução de lesões pulmonares devido a infecção causada pelo
              <em>Mycoplasma hyopneumoniae</em>.
              <br>O produto estimula o desenvolvimento da imunidade ativa em suínos contra
              <em>Mycoplasma hyopneumoniae</em>, também na presença de anticorpos de origem materna.
              <br>A duração da imunidade conferida pela vacina é de 22 semanas, a partir da vacinação.
            </span>
            <br>
          </div>
        </div>
        <div>
          <div class="paragraph-header">
            <span>Dosagem e Modo de Administração:</span>
          </div>
          <div class="paragraph-body">
            <span>A vacina deve ser administrada em suinos saudáveis com duas semanas de idade ou mais, com uma dose única de 0,2 mL, por via intradérmica, preferencialmente na lateral do pescoço, ao longo dos músculos das costas, utilizando dispositivo intradérmico adequado.</span>
            <br>
          </div>
        </div>
        <div>
          <div class="paragraph-header">
            <span>Recomendações Importantes:</span>
          </div>
          <ul class="list-list">
            <li>
              <span>Vacine somente animais saudáveis;</span>
            </li>
            <li>
              <span>Antes de usar a vacina, permita que a mesma atinja a temperatura ambiente;</span>
            </li>
            <li>
              <span>Agite bem antes de usar;</span>
            </li>
            <li>
              <span>Evitar a contaminação;</span>
            </li>
            <li>
              <span>Não utilizar durante a prenhez ou lactação;</span>
            </li>
            <li>
              <span>Não administrar a vacina misturada com qualquer outro produto veterinário;</span>
            </li>
            <li>
              <span>Não há informação disponível sobre a segurança e eficácia desta vacina quando utilizada com outro produto de uso veterinário. Portanto, a decisão de usá-la antes ou após qualquer outro produto precisa ser feita caso a caso, à critério do Médico Veterinário;</span>
            </li>
            <li>
              <span>Após aberto o frasco, utilizar todo o conteúdo;</span>
            </li>
            <li>
              <span>Restos do produto ou embalagens vazias devem ser descartados de forma adequada, de acordo com as exigências locais;</span>
            </li>
            <li>
              <span>Manter este ou qualquer outro medicamento fora do alcance de crianças e de animais domésticos.</span>
            </li>
          </ul>
        </div>
        <div>
          <div class="paragraph-header">
            <span>Precauções:</span>
          </div>
          <div class="paragraph-body">
            <span>Em caso de autoaplicação acidental, buscar orientação médica imediatamente e mostrar a bula ou rótulo ao médico.</span>
            <br>
          </div>
        </div>
        <div>
          <div class="paragraph-header">
            <span>Conservação:</span>
          </div>
          <div class="paragraph-body">
            <span>Conservar sob refrigeração, entre 2°C e 8°C, ao abrigo da luz. Não congelar.</span>
            <br>
          </div>
        </div>
        <div>
