<template>
  <div id="gallery">
    <h2 style="color:black;">Fotos</h2>
    <gallery :images="images" :index="index" @close="index = null"></gallery>
    <div class="container_box">
      <div class="box_images" id="box_images">
        <div
          class="image"
          v-for="(image, imageIndex) in images"
          :key="imageIndex"
          @click="index = imageIndex"
          :style="{ backgroundImage: 'url(' + image + ')', width: '300px', height: '200px' }"
        ></div>
      </div>

      <!-- Controles -->
      <transition name="fade">
        <div class="control-left" @click="controls('left')" v-show="isVisible">
          <i class="fas fa-arrow-left"></i>
        </div>
      </transition>
      <transition name="fade">
        <div class="control-right" @click="controls('right')" v-show="isVisible">
          <i class="fas fa-arrow-right"></i>
        </div>
      </transition>
    </div>
  </div>
</template>
<script>
import VueGallery from "vue-gallery";

export default {
  data: function() {
    return {
      images: [
        require("@/assets/gallery/img.jpg"),
        //   require('@/assets/gallery/img-1.png'),
        require("@/assets/gallery/img-2.jpg"),
        require("@/assets/gallery/img-2.png"),
        require("@/assets/gallery/img-3.jpg"),
        require("@/assets/gallery/img-4.jpg"),
        require("@/assets/gallery/img-5.jpg"),
        require("@/assets/gallery/img-6.jpg"),
        require("@/assets/gallery/img-7.jpg"),
        require("@/assets/gallery/img-8.jpg"),
        require("@/assets/gallery/img-9.jpg"),
        require("@/assets/gallery/img-10.jpg"),
        require("@/assets/gallery/img-11.jpg"),
        require("@/assets/gallery/img-12.jpg"),
        require("@/assets/gallery/img-13.jpg"),
        require("@/assets/gallery/img-14.jpg"),
        require("@/assets/gallery/img-15.jpg"),
        require("@/assets/gallery/img-16.jpg"),
        require("@/assets/gallery/img-17.jpg"),
        require("@/assets/gallery/img-18.jpg")
      ],
      index: null,
      isVisible: true
    };
  },
  components: {
    gallery: VueGallery
  },
  methods: {
    controls: function(direction) {
      var scroll_width_box_images = document.getElementById("box_images")
        .scrollWidth;
      var scroll_width_container_image = document.getElementById("gallery")
        .scrollWidth;
      var scroll_left_box_images = document.getElementById("box_images")
        .scrollLeft;
      var new_value_scroll_left_box_images;

      if (direction == "left") {
        new_value_scroll_left_box_images =
          scroll_left_box_images - scroll_width_container_image;
      } else {
        new_value_scroll_left_box_images =
          scroll_left_box_images + scroll_width_container_image;
      }

      document.getElementById(
        "box_images"
      ).scrollLeft = new_value_scroll_left_box_images;
    }
  }
};
</script>

<style lang="scss">
#gallery {
  // min-height: 100vh;
  overflow: auto;
  margin: 50px 0;

  .image {
    // float: left;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    border: 1px solid #ebebeb;
    margin: 5px;
    cursor: pointer;
  }

  .box_images {
    display: -webkit-box;
    overflow: hidden;
    width: auto;
  }

  .container_box {
    position: relative;
    overflow: hidden;

    [class*="control-"] {
      height: 100%;
      // width: 50px;
      position: absolute;
      top: 0;
      background: rgba(0, 0, 0, 0.2);
      cursor: pointer;
      user-select: none;
      opacity: 0;
      transition: opacity 0.2s ease-in;
    }
    i{
      position: relative;
      top: 50%;
      -webkit-transform: translateY(-50%);
      transform: translateY(-50%);
      font-size: 50px;
      color:white;
    }

    .control-left {
      left: 0;
    }

    .control-right {
      right: 0;
    }

    &:hover [class*="control-"] {
      opacity: 1;
    }
  }
}
</style>
