<template>
    <div id="DocView">
        <h2>Documentos</h2>
        
        <div class="box_files">
         
            <div v-for="(d,index) in files" :key="index" class="file">
                <span class="link" @click.stop="open(d)">
                    <img  :src ="d.thumb" />
                    <legend>{{d.name}}</legend>
                </span>                
            </div>        
        </div>
        <b-modal id="pdf-object-doc" :title="title"  size="xl"  centered v-model="pdfObject">
            <object type="application/pdf" :data="pdf" width="100%" height="500" style="height: 75vh;" v-if="!mobile">Navegador não tem suporte a PDF</object>
            <iframe :src="'https://docs.google.com/viewer?url=http://18.229.92.132/pwa_idal/' + pdf + '&embedded=true'" v-if="mobile" width="100%" height="500" frameborder="0"></iframe>
            <div slot="modal-footer" class="w-100">        
                <b-button
                variant="dark"
                size="sm"
                class="float-right"
                @click="$bvModal.hide('pdf-object-doc')"
                >
                Fechar
                </b-button>
                <a :href="pdf" :download="title" class="btn float-right btn-dark btn-sm download">Download</a>
            </div>
        </b-modal>
    </div>
</template>
<script>
import isMobile from 'mobile-device-detect';
import carousel from 'vue-owl-carousel';

export default {
    components:{
        carousel
    },
    data: () => ({
        files: [
            {
                name:"Manual do Usuário IDAL® System",                
                 uri:"dist/files/manual.pdf",
                 thumb:require("@/assets/thumb/manual.png")
            },
            {
                name:"Guia para uso IDAL® 3G",                
                 uri:"dist/files/guia.pdf",
                 thumb:require("@/assets/thumb/guia.png")
            }
        ],
        pdf:"",
        title:"",
        pdfObject: false,
        mobile: isMobile.isMobile
    }),
    methods:{
        open:function(el) {
            this.pdf = el.uri;
            this.title = el.name;
            this.pdfObject = true;
        },
        load: function() {
            this.pdf = this.files[this.$route.params.index].uri;
            this.title = this.files[this.$route.params.index].name;
            this.$bvModal.show('pdf-object-doc');
            this.pdfObject = true;

        }
    },
    created(){
        this.load();
    }
}
</script>
<style lang="scss" scoped>


    .btn.download{
        margin: 0 10px;
    }

  #DocView{
      
      .box_files{
        float: right;
        position: relative;
        left: -50%;
        
        & > .file {
            position: relative;
            left: 50%;
            float: left;
            
        }
    }

    h2{
        color:black !important;
        margin-bottom: 40px;
        margin-top:60px;
    }

    width: auto;
    overflow: auto;
    margin: 50px auto;
    padding: 0 10%;

    .link{
        cursor: pointer;        
        color: #028398;
        display: block;
        overflow: auto;

        &:hover{
            color: #074852;
        }
    }

    .file{
        // margin: 20px;
        // float: left;
        // width: 25%;
        padding: 5px 30px; 
    }

    img{
        border: 3px solid #333;
        border-radius: 5px;
    }

    min-height: 63vh;
  }

  legend{
    font-size: 1.4rem;
    text-align: center;
    // padding: 0 40px;
  }
</style>
