<template>
    <div id="files">
        <h2>Via Intradérmica</h2>
        <ul>
            <li v-for="(d,index) in files" :key="index">
                <a href="#" @click.stop="open(d)">
                    <img  :src ="d.thumb" />
                    <legend>{{d.name}}</legend>
                </a>                
            </li>
        </ul>
        <b-modal id="pdf-object" :title="title"  size="xl"  centered v-model="pdfObject">
            <object type="application/pdf" :data="pdf" width="100%" height="500" style="height: 75vh;" v-if="!mobile">Navegador não tem suporte a PDF</object>
            <iframe :src="'https://docs.google.com/viewer?url=http://18.229.92.132/pwa_idal/' + pdf + '&embedded=true'" v-if="mobile" width="100%" height="500" frameborder="0"></iframe>
            <div slot="modal-footer" class="w-100">        
                <b-button
                variant="dark"
                size="sm"
                class="float-right"
                @click="$bvModal.hide('pdf-object')"
                >
                Fechar
                </b-button>
                <a :href="pdf" :download="title" class="btn float-right btn-dark btn-sm download">Download</a>
            </div>
        </b-modal>
    </div>
</template>
<script>
import isMobile from 'mobile-device-detect';

export default {
    data: () => ({
        files: [
            {
                name:"Via intradérmica",                
                 uri:"dist/files/Via intradermica.pdf",
                 thumb:require("@/assets/thumb/intradermica4.png")
            },
            {
                name:"Rumo a outro patamar de vacinação em suínos",
                uri:"dist/files/Jolie, R. Towards a new standard in pig vaccination.pdf",
                thumb:require("@/assets/thumb/intradermica2.png")
            },
            {
                name:"A derme como principal local de aplicação de vacinas",
                uri:"dist/files/Summerfield, A. The dermis as a prime site of delivery.pdf",
                thumb:require("@/assets/thumb/intradermica3.png")
            }, 
        ],
        pdf:"",
        title:"",
        pdfObject: false,
        mobile: isMobile.isMobile
    }),
    methods:{
        open:function(el) {
            this.pdf = el.uri;
            this.title = el.name;
            this.pdfObject = true;
        }
    }
}
</script>
<style lang="scss" scoped>
  
  .btn.download{
      margin: 0 10px;
  }

  #files{
    width: auto;
    overflow: auto;
    margin: 110px 0;

    h2{
        color:black !important;
        margin-bottom: 80px;
    }

    ul{
        list-style: none;
        display: -webkit-inline-box;

    li{
        margin: 0 10px;
        width: 30%;
    }
    }

    img{
        border: 3px solid #333;
        border-radius: 5px;
    }

    legend{
        font-size: 0.79em;
    }

    // min-height: 69vh;

    

  }

    @media only screen and (max-width: 600px) {
        ul{
            display: unset;

            li{            
                width: 95%;
            }
        } 
    }
</style>
