<template>
  <div id="VideoFull">
    <!-- <video-player
      class="video-player-box"
      ref="videoPlayer"
      :options="video.playerOptions"
      :playsinline="true"
    ></video-player> -->
    <video controls :src="this.video.playerOptions.sources[0].src" autoplay></video>

    <div class="margin">-</div>
  </div>
</template>
<script>
export default {
  data: () => ({
    video: {
      playerOptions: {
        // videojs options
        autoplay: true,
        muted: false,
        language: "pt-br",
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: [
          {
            type: "video/mp4",
            src:
              ""
          }
        ]
      }
    },
    videos: [
      "dist/videos/MSD - VÍDEO IDAL_01 COMPONENTES E VISOR V02 - MAIO - 2019.mp4",
      "dist/videos/MSD - VÍDEO IDAL_02 COMO USAR V02 - MAIO - 2019.mp4",
      "dist/videos/MSD - VÍDEO IDAL_03 VACINAÇÃO V02 - MAIO - 2019.mp4",
      "dist/videos/MSD - VÍDEO IDAL_04 ARMAZENAMENTO E LIMPEZA V02 - MAIO - 2019.mp4",
      "dist/videos/MSD - VÍDEO IDAL_05 MANUTENÇÃO V02 - MAIO - 2019.mp4"
    ]
  }),
  methods: {
    load() {
      this.video.playerOptions.sources[0].src = this.videos[this.$route.params.index];
    }
  },
  created() {
    this.load();
  }
};
</script>
<style lang="scss">
#VideoFull {
  background: black;
//   margin: 88px auto auto auto;
//   height: 89vh;
  overflow: hidden;
  position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 100;

  .margin {
     margin-bottom: 140px;
  }

  video{
    max-width: 100%;
    height: auto;
    margin-top: 120px;
  }


  h2 {
    color: black !important;
    margin: 50px 0;
  }
  .video {
    margin: 0 10px;
    text-align: left;
    float: left;
  }

  .box_videos {
    // display: -webkit-box;
    overflow: auto;
    // width: auto;
    padding: 0 35px;
  }
  .container_box {
    overflow: auto;

    legend i {
      color: black;
    }

    .control-left {
      left: 0;
    }

    .control-right {
      right: 0;
    }

    &:hover [class*="control-"] {
      opacity: 1;
    }
  }
}
</style>
