<template>
  <div class="page">
    <slide-x-left-transition>
        <Cover v-show="this.$store.state.view_slide" />
    </slide-x-left-transition>
    <slide-x-left-transition>
        <DescricaoIdal v-show="!this.$store.state.view_slide" />
    </slide-x-left-transition>
    <div class="controls">
        <div class="item" @click="set(true)" :class="{active: this.$store.state.view_slide}"></div>
        <div class="item" @click="set(false)" :class="{active: !this.$store.state.view_slide}"></div>
    </div>
  </div>
</template>
<script>
import Cover from '@/components/Cover'
import DescricaoIdal from '@/components/DescricaoIdal'
import { setInterval, clearInterval } from 'timers';
import { SlideXLeftTransition } from 'vue2-transitions'
// import carousel from 'vue-owl-carousel'

export default {
  components:{
    Cover,
    DescricaoIdal,
    SlideXLeftTransition
    
  },
  data:() => ({
      view: true,
      interval: false
  }),
  methods:{
      change(){
           this.interval = setInterval(() => {
            this.$store.commit('update-view-slide', !this.$store.state.view_slide)
           }, 12000);
      },
      set(value){
          if(this.interval){
              clearInterval(this.interval);
          }
        //   this.view = value;
          this.$store.commit('update-view-slide', value)
      }
  },
  created(){
      this.view = true;
      this.change();
  },
  beforeDestroy(){
      clearInterval(this.interval);
  }
}
</script>
<style lang="scss">
.home{
    overflow-x: hidden;
    // margin-top: 20px;
}
.page{
    margin-top:100px;
}
.controls{
    position: fixed;
    top: 180px;
    z-index: 1;
    right: 40px;

    .item{
        float: left;
        width: 10px;
        cursor: pointer;
        height: 10px;
        border-radius: 25px;
        margin: 0 4px;
        border: 1px solid #636363;

        &.active{
            background: #636363;
        }
    }
}
</style>


